import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { useIntl, useTranslations } from "next-intl";
import React, { useMemo } from "react";
import Image from "next/image";
import { AlwaysOnOverlay, Item, Overlay } from "./Suggestions";
import { navigateToSearchResults, trackEvent } from "../utils";
import { useRouter } from "next/router";
import { ArrowRtlEnabled } from "./ArrowRtlEnabled";

export const RecentDestinations = () => {
  const t = useTranslations();
  const intl = useIntl();
  const { push, replace, query } = useRouter();

  const recentDestination = useMemo(() => {
    if (typeof window !== "undefined" && localStorage) {
      const prevDestination = JSON.parse(
        localStorage.getItem("destination") || "[]"
      );

      return prevDestination.filter((des: any) => {
        if (
          des &&
          des.departureDate &&
          new Date(des.departureDate) >= new Date()
        ) {
          return true;
        }

        return false;
      });
    }

    return [];
  }, []);

  const handleRecentSuggestion = (suggestion: any) => {
    navigateToSearchResults({
      searchState: {
        from: suggestion.from,
        to: suggestion.to,
        departureDate: new Date(suggestion.departureDate),
        returnDate: suggestion.returnDate && new Date(suggestion.returnDate),
      },
      t,
      push,
      replace,
      query,
    });
    trackEvent({
      action: "navigateToSearchResults",
      category: "handleRecentSuggestion",
      label: suggestion.to.code,
    });
  };

  if (!recentDestination?.length) {
      return null;
  }

  return (
    <Container maxWidth="lg" sx={{ my: 4 }}>
      <Container sx={{ p: { xs: 0, md: 3 } }}>
        <Box sx={{ flex: 1, mb: 0 }}>
          <Box sx={{ display: "flex", justifyContent: "space-between", mb: 4 }}>
            <Typography variant="h6">
              {t("home-page-recent-destination")}
            </Typography>
          </Box>

          <Grid container spacing={{ xs: 1, md: 2 }}>
            {recentDestination.map((suggestion: any, index: any) => {
              return (
                <Grid item xs={12} sm={4} key={index}>
                  <Item
                    sx={{
                      position: "relative",
                      height: 200,
                      borderRadius: 1,
                      overflow: "hidden",
                      "&:hover .overlay": {
                        opacity: 1,
                      },
                      cursor: "pointer",
                    }}
                    onClick={() => handleRecentSuggestion(suggestion)}
                  >
                    <Image
                      src={`https://assets.flightscanner.com/assets/images/cities/${suggestion.to.code.toUpperCase()}.jpg`}
                      layout="fill"
                      objectFit="cover"
                      alt={`image of city`}
                    />
                    <AlwaysOnOverlay />
                    <Overlay />
                    <Box
                      sx={{
                        position: "absolute",
                        bottom: 0,
                        left: 0,
                        right: 0,
                        textAlign: "left",
                        color: "#FFF",
                        p: 1,
                        display: "flex",
                        flexDirection: "row",
                      }}
                    >
                      <Box sx={{ flex: 1 }}>
                        <Typography
                          variant="subtitle2"
                          component="div"
                          sx={{ display: "flex" }}
                        >
                          {suggestion.from.city_name} <ArrowRtlEnabled />
                        </Typography>
                        <Typography
                          variant="body1"
                          component="div"
                          sx={{ fontSize: 22, fontWeight: "bold" }}
                        >
                          {suggestion.to.city_name}
                        </Typography>
                        <Typography variant="subtitle2" component="div">
                          {suggestion.to.country_name}
                        </Typography>
                        <Typography variant="subtitle2" component="div">
                          {intl.formatDateTime(
                            new Date(suggestion.departureDate),
                            {
                              month: "short",
                              day: "numeric",
                              weekday: "short",
                            }
                          )}
                        </Typography>
                      </Box>
                      <Box sx={{ alignSelf: "flex-end" }}>
                        {suggestion.returnDate &&
                          intl.formatDateTime(new Date(suggestion.returnDate), {
                            month: "short",
                            day: "numeric",
                            weekday: "short",
                          })}
                      </Box>
                    </Box>
                  </Item>
                </Grid>
              );
            })}
          </Grid>
        </Box>
      </Container>
    </Container>
  );
};
