export const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const absSize = {
  position: "absolute",
  top: "0px",
  left: "0px",
  width: "100%",
  height: "100%",
};

export const AlwaysOnOverlay = () => {
  return (
    <Box
      sx={{
        background:
          "linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 45%, rgba(0, 0, 0, 0.6) 100%)",
        ...absSize,
      }}
    ></Box>
  );
};

export const Overlay = () => {
  return (
    <Box
      className="overlay"
      sx={{
        backgroundColor: "rgba(0, 0, 0, 0.6)",
        opacity: 0,
        transitionDuration: "0.3s",
        transitionProperty: "opacity",
        ...absSize,
      }}
    ></Box>
  );
};
const suggestionCountByDevice = 9;

