import ArrowRight from "@mui/icons-material/ArrowRightAlt";

import { useTheme } from "../theme";

export const ArrowRtlEnabled = () => {
  const theme = useTheme();

  if (theme.direction === "rtl") {
    return <ArrowRight style={{ transform: 'rotate(180deg)' }} />;
  }
  return <ArrowRight />;
};
