export async function getStaticProps({ locale }: GetStaticPropsContext) {
  // function onlyUnique(value: any, index: any, self: any) {
  //   return self.indexOf(value) === index;
  // }
  const localeToLocation = {
    cs: {
      iata: "PRG",
      name: "Praha",
      country_name: "Česká republika",
    },
    ru: {
      iata: "MOW",
      name: "Москва",
      country_name: "Россия",
    },
    de: {
      iata: "BER",
      name: "Berlin",
      country_name: "Germany",
    },
    es: {
      iata: "BCN",
      name: "España",
      country_name: "Barcelona",
    },
    el: {
      iata: "ATH",
      name: "Αθήνα",
      country_name: "Ελλάδα",
    },
    fr: {
      iata: "PAR",
      name: "Paris",
      country_name: "France",
    },
    it: {
      iata: "ROM",
      name: "Roma",
      country_name: "Italia",
    },
    id: {
      iata: "JKT",
      name: "Jakarta",
      country_name: "Indonesia",
    },
    ko: {
      iata: "SEL",
      name: "서울특별시",
      country_name: "대한민국",
    },
    pl: {
      iata: "WAW",
      name: "Warszawa",
      country_name: "Polska",
    },
    pt: {
      iata: "BSB",
      name: "Brasília",
      country_name: "Brasil",
    },
    th: {
      iata: "BKK",
      name: "กรุงเทพ",
      country_name: "ไทย",
    },
    vi: {
      iata: "HAN",
      name: "Hà Nội",
      country_name: "Việt Nam",
    },
    he: {
      iata: "TLV",
      country_name: "ישראל",
      name: "נמל התעופה בן-גוריון",
    },
    ar: {
      iata: "DWC",
      country_name: "الإمارات العربية المتحدة",
      name: "مطار آل مكتوم الدولي",
    },
  } as any;

  const location = (locale && localeToLocation[locale]) || {
    iata: "LON",
    name: "London",
    country_name: "United Kingdom",
  };

  const ratesUrl = `https://apiv2.flightscanner.com/rates`;
  // const pricesUrl = priceMapPrices(locale as string, "PFO", false);
  // const routesUrl = priceMapSupportedRoutes(locale as string, "PFO", false);

  // MOVE THIS TO HOOK SO FALLBACK CAN WORK AND ADD FALLBACK UNDER FROM CHANGE SO IT WILL RERFRESH
  // SECOND STEP ADD UI CITY FROM SO IT'S CLEAR
  const urlSuggestion = suggestionsV2URL(location.iata, locale);
  const suggestions = await fetch(urlSuggestion).then((r) => r.json()).catch((error) => {
    if (error?.name !== "AbortError") {
      captureException(error);
    }
  });
  const rates = await fetch(ratesUrl).then((r) => r.json()).catch((error) => {
    if (error?.name !== "AbortError") {
      captureException(error);
    }
  });
  // const prices = await fetch(pricesUrl).then((r) => r.json());
  // const routes = await fetch(routesUrl).then((r) => r.json());
  return {
    props: {
      location,
      // You can get the messages from anywhere you like. The recommended
      // pattern is to put them in JSON files separated by language and read
      // the desired one based on the `locale` received from Next.js.
      messages: (await import(`../messages/index/${locale || 'en'}.json`)).default,
      // suggestions: {
      //   cheap: cheapSuggestions,
      //   // popular: suggestions ? suggestions.sort((a: any, b: any) => new Date(b.depart_date).getTime() - new Date(a.depart_date).getTime()) : [],
      // },
      fallback: {
        [ratesUrl]: rates,
        [urlSuggestion]: suggestions,
        // [pricesUrl]: prices,
        // [routesUrl]: routes,
      },
    },
    revalidate: 60 * 5,
  };
}

export default Home;
