import { useMemo } from "react";
import { useSWRNFetchImmutable } from "./useSwr";

export const suggestionsV2URL = (from: string, locale: string = 'en') =>
  `https://suggest.travelpayouts.com/widgets/v1/flight-map?origin=${from}&locale=${locale}&currency=rub&value_min=0&value_max=1000000&only_direct=true&round_trip=true`;

export const parseSuggestions = (suggestions: any) => {
  if (!suggestions?.items) {
    return [];
  }

  const mappedItems = suggestions.items.map((item: any) => ({
    ...item.properties,
  }));

  return mappedItems
    .sort((a: any, b: any) => a.price - b.price)
    .filter(
      (item: any, idx: any) =>
        idx === mappedItems.findIndex((other: any) => item.code === other.code)
    );
};

export const useSuggestionsV2 = ({ from, locale }: any) => {
  const { data, error } = useSWRNFetchImmutable(
    `https://suggest.travelpayouts.com/widgets/v1/flight-map?origin=${from}&locale=${locale}&currency=rub&value_min=0&value_max=1000000&only_direct=true&round_trip=true`
  );

  const suggestions = useMemo(() => {
    if (data){
      return parseSuggestions(data);
    }
  }, [data]);

  return {
    suggestions,
    isLoading: !error && !data,
    isError: error
  };
};
